.form-area {
  width: 60%;
  max-width: 800px;
  margin: 40px auto;
  text-align: center;
}
.name,
.email {
  text-align: left;
}

#name,
#email,
#message,
.dropdown {
  border: 1px solid $mintcolor;
  background: transparent;
  padding: 1em;
  display: block;
  width: 100%;
  margin-top: 1em;
  margin-bottom: 2em;
  resize: none;
}

#message {
  height: 150px;
}

.half {
  float: left;
  width: 48%;
  margin-bottom: 1em;
}

.left {
  margin-right: 2%;
}

.right {
  width: 50%;
}

#input-submit {
  max-width: 300px;
  cursor: pointer;
  border: 1px solid $mintcolor;
  padding: 10px 15px;
  font-family: $mainfont;
  background: $mintcolor;
  transition: all 0.2s ease-in-out;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  &:hover {
    border: 1px solid $yellowcolor;
  }
}
