//!!about section!!//
.about {
  text-align: left;

  aside {
    width: 48%;
    background: $superlightgreycolor;
    display: inline-block;
    &:nth-of-type(1) {
      margin-right: 1%;
    }
    &:nth-of-type(2) {
      margin-left: 1%;
    }

    p,
    h2 {
      padding: 10px;
    }
  }
}
